import React, { Component } from 'react'

export default class Animales extends Component {
    constructor(props) {
        super(props)
        this.state = {
            listaMascotas: []
        }
    }

    componentDidMount() {
        const rutaServicio = "http://localhost/servicios-web/listaanimales.php";
        fetch(rutaServicio)
            .then(
                res => res.json()
            )
            .then(
                (result) => {
                    console.log(result);
                    this.setState({
                        listaMascotas: result
                    })
                }
            )
    }

    dibujarTabla(datosTabla) {
        return (
            <div className="row row-cols-1 row-cols-md-3 g-4">
                {datosTabla.map(itemMascota =>
                    <div className="col" key={itemMascota.idempleado}>
                        <div className="card">
                            <img src={"http://localhost/servicios-web/mascotas/" + itemMascota.foto} class="card-img-top" alt="..." />
                            <div className="card-body">
                                <h5 className="card-title">{itemMascota.descripcion}</h5>
                                <p className="card-text"> {itemMascota.tipo} {itemMascota.nombre}
                                </p>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        )
    }

    render() {
        let contenidoTable = this.dibujarTabla(this.state.listaMascotas);
        return (
            <section className="padded">
                <div className="container">
                    <h2>Mascotas</h2>
                    {contenidoTable}
                </div>
            </section>
        )
    }
}

