import React, { Component } from 'react'

export default class TablaMascotas extends Component {
    constructor(props) {
        super(props)
        this.state = {
            listaMascotas: [],
            mascotaSeleccionada: "",
            nombreAgregar: "",
            descripcionAgregar: ""
        }
    }

    componentDidMount() {
        const rutaServicio = "http://localhost/servicios-web/mascotas.php";
        fetch(rutaServicio)
            .then(
                res => res.json()
            )
            .then(
                (result) => {
                    console.log(result);
                    this.setState({
                        listaMascotas: result
                    })
                }
            )
    }

    dibujarTabla(datosTabla) {
        return (
            <table className="table">
                <thead>
                    <tr>
                        <th>Cod</th>
                        <th>Nombre</th>
                        <th>Descripción</th>
                    </tr>
                </thead>
                <tbody>
                    {datosTabla.map(itemMascota =>
                        <tr key={itemMascota.idmascota}>
                            <th>{itemMascota.idmascota }</th>
                            <th>{itemMascota.nombre}</th>
                            <th>{itemMascota.descripcion}</th>
                        </tr>
                    )}
                </tbody>
            </table>
        )
    }

    dibujarFormularioAgregar() {
        return (
            <div>
                <div className="mb-3">
                    <button className="btn btn-primary" data-bs-toggle="collapse" data-bs-target="#collapseExample">Nueva categoría</button>
                </div>
                <div className="collapse" id="collapseExample">
                    <div className="card card-body">
                        <form onSubmit={this.mascotaInsertar}>
                            <div className="mb-3">
                                <input type="text" placeholder="Mascota" className="form-control"
                                    onChange={(e) => this.setState({ nombreAgregar: e.target.value })} />
                            </div>
                            <div className="mb-3">
                                <input type="text" placeholder="Descripción" className="form-control"
                                    onChange={(e) => this.setState({ descripcionAgregar: e.target.value })} />
                            </div>
                            <div className="mb-3">
                                <button type="submit" className="btn btn-primary" data-bs-toggle="collapse" data-bs-target="#collapseExample">Agregar</button>
                                <button type="button" className="btn btn-primary" data-bs-toggle="collapse" data-bs-target="#collapseExample">Cerrar</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }

    mascotaInsertar = (e) => {
        e.preventDefault();
        const rutaServicio = "http://localhost/servicios-web/registrarmascotas.php";
        var formData = new FormData();
        formData.append("nombre", this.state.nombreAgregar)
        formData.append("descripcion", this.state.descripcionAgregar)
        fetch(rutaServicio, {
            method: 'POST',
            body: formData
        })
            .then(
                res => res.text()
            )
            .then(
                (result) => {
                    console.log(result);
                }
            )
    }

    render() {
        let contenidoFormularioAgregar = this.dibujarFormularioAgregar();
        let contenidoTable = this.dibujarTabla(this.state.listaMascotas);
        return (
            <section className="padded">
                <div className="container">
                    <h2>Mis Mascotas</h2>
                    {contenidoFormularioAgregar}
                    {contenidoTable}
                </div>
            </section>
        )
    }
}
