import React from 'react';
import './MainFooter.css'
import icon1 from '../assets/icon/icon1.gif'
import icon2 from '../assets/icon/icon2.gif'
import icon3 from '../assets/icon/icon3.gif'
import icon4 from '../assets/icon/icon4.gif'

function MainFooter(props) {
	return (
		<div className="container">
			<div className="redes">
				<ul>
					<li><a href="/"><img src={icon1} alt="" /></a></li>
					<li><a href="/"><img src={icon2} alt="" /></a></li>
					<li><a href="/"><img src={icon3} alt="" /></a></li>
					<li><a href="/"><img src={icon4} alt="" /></a></li>
				</ul>
			</div>

			<div className="footer">
				<p>Todos los derechos reservados - 2021</p>
			</div>
		</div>
	);
}

export default MainFooter;