import React, { Component } from 'react'

export default class ComidaMascotas extends Component {

    constructor(props) {
        super(props)
        this.state = {
            listaComida: []
        }
    }

    componentDidMount() {
        const rutaServicio = "http://localhost/servicios-web/comidaanimales.php";
        fetch(rutaServicio)
            .then(
                res => res.json()
            )
            .then(
                (result) => {
                    console.log(result);
                    this.setState({
                        listaComida: result
                    })
                }
            )
    }

    dibujarTabla(datosTabla) {
        return (
            <table className="table">
                <thead>
                    <tr>
                        <th>Cod</th>
                        <th>Para</th>
                        <th>Marca</th>
                    </tr>
                </thead>
                <tbody>
                    {datosTabla.map(itemComida =>
                        <tr key={itemComida.idcomida}>
                            <th>{itemComida.idcomida}</th>
                            <th>{itemComida.Para}</th>
                            <th>{itemComida.Marca}</th>
                        </tr>
                    )}
                </tbody>
            </table>
        )
    }

    render() {
        let contenidoTable = this.dibujarTabla(this.state.listaComida);
        return (
            <section className0="padded" id="comida-mascota">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <h1>Comida Mascota</h1>
                            <div className="col">
                                {contenidoTable}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}