import React from 'react';
import { Link } from 'react-router-dom';

function MainNav(props) {
  return (
    <div id="menu" className="bg-dark">
            <div className="container-nav">
                <nav className="navbar navbar-expand-lg navbar-light">
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="Navegation">
                      <div className="nav navbar-nav w-100 justify-content-center " >
                        <a className="nav-item nav-link active" href="/">Inicio</a>
                        <Link className="nav-item nav-link" to="/animales">Mascotas</Link>
                        <Link className="nav-item nav-link" to="/tablamascotas">TablaMascotas</Link>
                        <a className="nav-item nav-link" href="/">Nombres</a>
                        <a className="nav-item nav-link" href="/">Adopción</a>
                      </div>
                    </div>
                  </nav>
            </div>
        </div>
  );
}

export default MainNav;